  async checkCurrentUser(){
    firebaseApp.auth().onAuthStateChanged((user) => {
      if (user != null) {
        this.setState({currentUser : user, login: true})
        console.log(user.email)
        user.getIdTokenResult().then((idTokenResult) => {
          console.log(idTokenResult.claims.admin)
        })
        console.log('We are authenticated now!')
        this.tradeListener() 
      }else{
        console.log('login out')
      }
    })
    console.log('login? '+this.state.login)
  }

  validateLoginForm() {
    return this.state.email.length > 0 && this.state.password.length > 0;
  }

  handleLoginChange = event => {
    this.setState({
      [event.target.id]: event.target.value
    });
  }

  handleLoginSubmit = async(event) => {
    event.preventDefault();
    await this.login()
  }

  async login(){
    console.log('LOGIN');//('waixiong1997@hotmail.com', '1997@hotmail.com')
    firebaseApp.auth().signInWithEmailAndPassword(this.state.email, this.state.password).then((user)=>{
      this.setState({currentUser : user.user, login: true})
      user.user.getIdTokenResult().then((idTokenResult) => {
        console.log(idTokenResult.claims.admin)
      })
    }).then(function(){
      this.tradeListener()
    }.bind(this));
  }

  logout(){
    firebaseApp.auth().signOut()
    console.log('Sign Out')
    this.setState({login: false})
  }

  componentWillMount(){
    this.checkCurrentUser()
    //this.orderListener()
    //this.tradeListener()
    this.nationListListener()
    this.sessionListener()
    this.contractListener()
    console.log('set completed')
  }

  async updatePrice(type, price, resource){
    var d = firebaseApp.database().ref().child('trade').child(resource + '/data/' + type)
    const transaction = await d.transaction(function(post){
      post = price
      return post
    })
    console.log('price updated : ' + transaction.committed)
  }

  nationListListener(){
    const list = firebaseApp.database().ref().child('idToName')
    list.on('child_added', snap => {
      this.nationOptions.push({ value: snap.key, label: snap.val()['name'] })
      this.nationMap[snap.key] = snap.val()
      this.setState({})
    })
  }

  sessionListener(){
    console.log('SET session listener')
    const list = firebaseApp.database().ref().child('session')
    list.on('value', snap => {
      this.setState({session : snap.val()})
      var contracts = Object.keys(this.contractList)
      contracts.forEach(c => {
        if(this.contractList[c]['expiry'] < this.state.session){
          delete this.contractList[c]
        }
      })
    })
  }

  contractListener(){
    console.log('SET COntract listener')
    const contractRef = firebaseApp.database().ref().child('contract')
    contractRef.on('child_added', snap => {
      console.log('contract '+snap.key)
      if(snap.val()['expiry'] >= this.state.session){
        this.contractList[snap.key] = snap.val()
        this.setState({})
      }
    })
    contractRef.on('child_changed', snap => {
      var parties = Object.keys(snap.val()['parties'])
      if(snap.val()['expiry'] >= this.state.session){
        this.contractList[snap.key] = snap.val()
        this.setState({})
      }else{
        delete this.contractList[snap.key]
      }
    })
    contractRef.on('child_removed', snap => {
      delete this.contractList[snap.key]
    })
  }

  tradeListener(){
    const exchange = firebaseApp.database().ref().child('trade')
    const resourcesList = Object.keys(this.state.tradeData)
    resourcesList.forEach((k) => {
      var d = exchange.child(k + '/data')
      var b = exchange.child(k + '/Bid')
      var a = exchange.child(k + '/Ask')
      d.on('child_added', snap => {
        this.state.tradeData[k]['price'][snap.key] = snap.val()
      }, err => console.log('trade data ' + err))
      d.on('child_changed', snap => {
        console.log('price change')
        this.state.tradeData[k]['price'][snap.key] = snap.val()
      }, err => console.log('trade data ' + err))
      b.on('child_added', snap => {
        this.state.tradeData[k]['Bid'][snap.key] = snap.val()
        if(this.state.tradeData[k].bids.length == 0){
          this.state.tradeData[k].bids.push(snap.key)
          this.updatePrice('BidPrice', snap.val()['price'], k)
        }else{
          var done = false
          var l = this.state.tradeData[k].bids.length;
          for(var i = 0; i < l; i++){
            if(snap.val()['price'] > this.state.tradeData[k].Bid[this.state.tradeData[k].bids[i]]['price']){
              this.state.tradeData[k].bids.splice(i, 0, snap.key)
              done = true
              if(i == 0){
                this.updatePrice('BidPrice', snap.val()['price'], k)
              }
              break;
            }
          }
          if(!done){
            this.state.tradeData[k].bids.push(snap.key)
          }
        }
      }, err => console.log('trade bid ' + err))
      b.on('child_changed', snap => {
        this.state.tradeData[k]['Bid'][snap.key] = snap.val()
      }, err => console.log('trade bid ' + err))
      b.on('child_removed', snap => {
        this.state.tradeData[k]['Bid'][snap.key] = undefined
        for(var i = 0; i < this.state.tradeData[k].bids.length; i++){
          if(this.state.tradeData[k].bids[i] == snap.key){
            this.state.tradeData[k].bids.splice(i, 1)
            if(i == 0 && this.state.tradeData[k].bids.length > 0){
              this.updatePrice('BidPrice', this.state.tradeData[k]['Bid'][this.state.tradeData[k].bids[i]]['price'], k)
            }
            break;
          }
        }
      }, err => console.log('trade bid ' + err))
      a.on('child_added', snap => {
        this.state.tradeData[k]['Ask'][snap.key] = snap.val()
        if(this.state.tradeData[k].asks.length == 0){
          this.state.tradeData[k].asks.push(snap.key)
          this.updatePrice('AskPrice', snap.val()['price'], k)
        }else{
          var done = false
          var l = this.state.tradeData[k].asks.length;
          console.log('start with '+this.state.tradeData[k].asks)
          for(var i = 0; i < l; i++){
            console.log('Ask ' + i)
            if(snap.val()['price'] < this.state.tradeData[k].Ask[this.state.tradeData[k].asks[i]]['price']){
              console.log('smaller')
              this.state.tradeData[k].asks.splice(i, 0, snap.key)
              done = true
              if(i == 0){
                console.log('first')
                this.updatePrice('AskPrice', snap.val()['price'], k)
              }
              break;
            }
            console.log('loop == '+this.state.tradeData[k].asks)
          }
          if(!done){
            console.log('last')
            this.state.tradeData[k].asks.push(snap.key)
          }
          console.log('last is '+this.state.tradeData[k].asks)
        }
      }, err => console.log('trade ask ' + err))
      a.on('child_changed', snap => {
        this.state.tradeData[k]['Ask'][snap.key] = snap.val()
      }, err => console.log('trade ask ' + err))
      a.on('child_removed', snap => {
        this.state.tradeData[k]['Ask'][snap.key] = undefined
        for(var i = 0; i < this.state.tradeData[k].asks.length; i++){
          if(this.state.tradeData[k].asks[i] == snap.key){
            this.state.tradeData[k].asks.splice(i, 1)
            if(i == 0 && this.state.tradeData[k].asks.length > 0){
              this.updatePrice('AskPrice', this.state.tradeData[k]['Ask'][this.state.tradeData[k].asks[i]]['price'], k)
            }
            break;
          }
        }
      }, err => console.log('trade ask ' + err))
    })
  }

  routePush(path){
    this.props.history.push(path);
  }

  tick() {
    this.setState(state => ({
      seconds: state.seconds + 1
    }));
  }

  componentDidMount() {
    this.interval = setInterval(() => this.tick(), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
    //this.contractListener()
  }

  reset() {
    this.setState({
      seconds: 0
    });
  }

